import React, { useEffect } from 'react';
import {useHistory,withRouter, Link} from 'react-router-dom';
import {useSelector, useDispatch, connect} from 'react-redux';
import { useAuth } from 'contexts/AuthContext';
import { useWallet } from '@binance-chain/bsc-use-wallet';

const UserInfo = (props) => {
    
    const {account } = useWallet() 
    
    
    return (
        <ul className="navbar-nav action">
                
                {!account && window.localStorage.getItem('accountStatus')?
                    <li className="nav-item ml-3">
                        <Link to={'/connect'} className="btn ml-lg-auto btn-bordered-white">Wallet Connect</Link>
                    </li>
                    :
                    <li className="nav-item">
                        <a href="#" className="nav-link" data-toggle="modal" data-target="#user">
                            <img className="rounded-circle" src={'/img/auction_10.jpg'} width={50} alt="" />
                        </a>
                        
                    </li>
                }
            </ul>
    );
};

export default UserInfo;