import React, { Component, useEffect, useState } from 'react';
import {useHistory,withRouter, Link} from 'react-router-dom';
import axios from 'axios';
import { useWallet } from '@binance-chain/bsc-use-wallet';


const initData = {
    "img": "/img/logo.svg",
    "content": "Nutgain - The revolutionalry NFT Platform, designed for ambitions artists, collectors, and traders.",
    "widget_1": "Useful Links",
    "widget_2": "My Account",
    "widget_3": "Subscribe Us",
    "socialData": [
    
    {
        "id": 1,
        "link": "https://t.me/joinchat/7dHMSaFrwwZiZDMx",
        "icon": "fab fa-telegram"
    },
    {
        "id": 2,
        "link": "https://twitter.com/Kaiken_inu",
        "icon": "fab fa-twitter"
    },
    {
        "id": 3,
        "link": "http://www.kaikeninu.net/",
        "icon": "fab fa-vine"
    },
    {
        "id": 4,
        "link": "https://www.reddit.com/r/KaikenInu_Official/",
        "icon": "fab fa-reddit"
    }
    ],
    "widgetData_1": [
    {
        "id": 1,
        "text": "All NFTs",
        "link" : 'auctions'
    },
    {
        "id": 3,
        "text": "Create",
        "link" : 'create'
    },
    {
        "id": 4,
        "text": "Explore",
        "link" : 'explore'
    },
    ],
    "widgetData_2": [
    {
        "id": 1,
        "text": "Profile",
        "link" : 'user'
    },
    {
        "id": 2,
        "text": "Setting",
        "link" : 'setting'
    },
    {
        "id": 3,
        "text": "MyCollections",
        "link" : 'mycollections'
    },
    
    ]
}

function Footer(){
    const {account} = useWallet();
    const [data, setData] = useState(initData);
    const [socialData, setSocialData] = useState(initData.socialData);
    const [widgetData_1, setWidgetData1] = useState(initData.widgetData_1);
    const [widgetData_2, setWidgetData2] = useState(initData.widgetData_2);
    return (
        <footer className="footer-area">
            {/* Footer Top */}
            <div className="footer-top">
                <div className="container">
                    <div className="row">
                        <div className="col-12 col-sm-6 col-lg-3 res-margin">
                            {/* Footer Items */}
                            <div className="footer-items">
                                {/* Logo */}
                                <a className="navbar-brand" href="/">
                                    <img src={data.img} alt="" />
                                </a>
                                <p>{data.content}</p>
                                {/* Social Icons */}
                                <div className="social-icons d-flex">
                                    {socialData.map((item, idx) => {
                                        return (
                                            <a key={`sd_${idx}`} className={item.link} target="_blank" href={`${item.link}`}>
                                                <i className={item.icon} />
                                                <i className={item.icon} />
                                            </a>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>
                        <div className="col-12 col-sm-6 col-lg-3 res-margin">
                            {/* Footer Items */}
                            <div className="footer-items">
                                {/* Footer Title */}
                                <h4 className="footer-title">{data.widget_1}</h4>
                                <ul>
                                    {widgetData_1.map((item, idx) => {
                                        return (
                                            <li key={`wdo_${idx}`}><Link to={`/${item.link}`}>{item.text}</Link></li>
                                        );
                                    })}
                                </ul>
                            </div>
                        </div>
                        <div className="col-12 col-sm-6 col-lg-3 res-margin">
                            {/* Footer Items */}
                            <div className="footer-items">
                                {/* Footer Title */}
                                <h4 className="footer-title">{data.widget_2}</h4>
                                <ul>
                                    {widgetData_2.map((item, idx) => {
                                        return (
                                            <li key={`wdo_${idx}`}><Link to={account?`/${item.link}/${account}`:`/connect`}>{item.text}</Link></li>
                                        );
                                    })}
                                </ul>
                            </div>
                        </div>
                        <div className="col-12 col-sm-6 col-lg-3">
                            {/* Footer Items */}
                            <div className="footer-items">
                                {/* Footer Title */}
                                <h4 className="footer-title">{data.widget_3}</h4>
                                {/* Subscribe Form */}
                                <div className="subscribe-form d-flex align-items-center">
                                    <input type="email" className="form-control" placeholder="info@yourmail.com" />
                                    <button type="submit" className="btn"><i className="icon-paper-plane" /></button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* Footer Bottom */}
            <div className="footer-bottom">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            {/* Copyright Area */}
                            
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    );
}

export default Footer;