import React from 'react';
import {useHistory,withRouter, Link} from 'react-router-dom';

const ModalNavMenu = () => {
    const history = useHistory();
    const Route = (router) => {
        history.push('/' + router);
    }
    return (
        <div id="nav_menu" className="modal fade p-0">
            <div className="modal-dialog dialog-animated">
                <div className="modal-content h-100">
                    <div className="modal-header" data-dismiss="modal">
                        Menu <i className="far fa-times-circle icon-close" />
                    </div>
                    <div className="menu modal-body">
                        <div className="row align-self-center">
                            <div className='col-12'>
                                <div style={{ backgroundColor: 'whitesmoke', height: 1, marginBottom: '12', marginTop: '12' }}></div>
                            </div>
                            <div className="col-12">
                                <button onClick={() => Route('home')} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'Home'}</button>
                            </div>
                            <div className="col-12">
                                <button onClick={() =>  Route('collections')} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'Collections'}</button>
                            </div>
                            
                            <div className="col-12">
                                <button onClick={() =>  Route('create')} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'Create'}</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ModalNavMenu;