import Web3 from 'web3'
import { AbiItem } from 'web3-utils'
import KaiECOABI from 'constants/abi/KaiECOABI.json'
import NFTCollectionABI from 'constants/abi/NFTCollectionABI.json'
import NFTMarketplaceABI from 'constants/abi/NFTMarketPlaceABI.json'
import KaiECOERC721ABI from 'abis/KaiECOToken721.json'
import KaiECOMarketplaceABI from 'abis/KaiECOMarketplace.json'
import KaiECOMarketplaceV2 from 'constants/abi/KaiECOMarketplaceV2.json'
import Bep20ABI from 'constants/abi/Bep20.json';
import web3NoAccount from 'utils/web3'

const getContract = (abi: any, address: string, web3?: Web3) => {
    const _web3 = web3 ?? web3NoAccount
    return new _web3.eth.Contract((abi as unknown) as AbiItem, address)
}

export const getBep20Contract = (address: string, web3?: Web3) => {
    return getContract(Bep20ABI, address, web3);
}

export const getNFTCollectionContract = (address : string, web3?:Web3) => {
    return getContract(NFTCollectionABI, address, web3)
}

export const getNFTMarketplaceContract = (address : string, web3?:Web3) => {
    return getContract(NFTMarketplaceABI, address, web3)
}

export const getKaiECOERC721Contract = (address : string, web3?: Web3) =>{
    return getContract(KaiECOERC721ABI.abi, address, web3);
}

export const getKaiECOMarketplaceContract = (address : string, web3?: Web3) => {
    return getContract(KaiECOMarketplaceV2, address, web3);
}

export const getKaiCollectionContract = (abi: any, web3?: Web3) => {
    const _web3 = web3??web3NoAccount;
    return new _web3.eth.Contract(abi);
}

export const getMintContract = (address: string, web3?: Web3) => {
    return getContract(KaiECOERC721ABI.abi, address, web3);
}

export const getKaiECOContract = (address: string, web3?: Web3) => {
    return getContract(KaiECOABI, address, web3);
}

