
import React, { useEffect, useState } from 'react';
import {Link, useHistory} from 'react-router-dom';
import {  useBNBBalance, useTokenBalance } from 'hooks/useTokenBalance';
import { getBalanceNumber } from 'utils/formatBalance';
import { useAuth } from 'contexts/AuthContext';
import { useWallet } from '@binance-chain/bsc-use-wallet';
import { PadWalletAddress } from 'utils/padwallet';
import contracts from 'constants/contracts';
import { tokens } from 'constants/tokens';

const Nutgain = tokens.Nutgain;



const ModalUser = () => {
    const wallet = useWallet();
    const [account, setAccount] = useState(PadWalletAddress(wallet.account))
    const history = useHistory();
    
    const kaiECO_Balance = useTokenBalance(Nutgain.address);
    const bnb_Balance = useBNBBalance();
   
    const Signout = () => {
        localStorage.removeItem("accountStatus");
        wallet.reset();
        history.push('/connect')
    }

    const Users = () => {
        
        history.push("/user/" + wallet.account)
    }

    const Setting = () => {
        history.push('/setting/' + wallet.account)
    }

    const Collections = () => {
        history.push('/mycollections/' + wallet.account)
    }

    return (
        <div id="user" className="modal fade p-0">
            <div className="modal-dialog dialog-animated">
                <div className="modal-content h-100" style={{backgroundColor:'rgb(31,31,31)'}}>
                    <div className="modal-header" data-dismiss="modal">
                        User Setting <i className="far fa-times-circle icon-close" />
                    </div>
                    <div className="modal-body" style={{ alignItems: 'baseline' }}>
                        <div className="row">
                            <div className="col-12">
                                <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">{account}</span>
                                <p style={{ margin: '0.5em 0 0.5em 0', fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3"><a href='#' className=" mt-0">Set display name</a></p>
                            </div>
                            <div className="col-12">
                                <div className='single-seller d-flex align-items-center'>
                                    <img className="avatar-sm rounded-circle" style={{'width' : 50, 'height' : 50}} src="/token/nutgain.png" alt="balance" />
                                    <div className='seller-info ml-3'>
                                        <p style={{ margin: 0, fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{'Nutgain Balance'}</p>
                                        {kaiECO_Balance.toNumber() === 0?
                                             <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{'0'}</span>
                                            :
                                            <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{getBalanceNumber(kaiECO_Balance, Nutgain.decimal)}</span>
                                        }
                                    </div>   
                                </div>
                            </div>
                            <div className='col-12'>
                                <div style={{ backgroundColor: 'whitesmoke', height: 1, marginBottom: '12', marginTop: '12' }}></div>
                            </div>
                            <div className='col-12'>
                                <div style={{ backgroundColor: 'whitesmoke', height: 1, marginBottom: '12', marginTop: '12' }}></div>
                            </div>
                            <div className="col-12">
                                <div className='single-seller d-flex align-items-center'>
                                    <img className="avatar-sm rounded-circle" style={{'width' : 50, 'height' : 50}} src="/token/wBNB.png"  alt="balance" />
                                    <div className='seller-info ml-3'>
                                        <p style={{ margin: 0, fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{'BNB Balance'}</p>
                                        {bnb_Balance.toNumber() === 0?
                                             <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{'0'}</span>
                                            :
                                            <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0" >{getBalanceNumber(bnb_Balance, 18)}</span>
                                        }
                                    </div>
                                </div>
                            </div>
                            <div className='col-12'>
                                <div style={{ backgroundColor: 'whitesmoke', height: 1, marginBottom: '12', marginTop: '12' }}></div>
                            </div>

                            <div className='col-12'>
                                <div style={{ backgroundColor: 'whitesmoke', height: 2, marginBottom: '12', marginTop: '12' }}></div>
                            </div>
                            <div className="col-12">
                                <button onClick={() => Collections()} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'My Collections'}</button>
                            </div>
                            
                            <div className="col-12">
                                <button onClick={() => Setting()} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'Edit Profile'}</button>
                            </div>
                            <div className="col-12">
                                <button onClick={() => Signout()} style={{textAlign:'left'}} className="btn btn-bordered-white-user mt-3">{'Sign out'}</button>
                            </div>
                        </div>


                    </div>
                </div>
            </div>
        </div>
    );
};

export default ModalUser;