import contracts from 'constants/contracts'
import KaiECO721 from 'abis/KaiECOToken721.json';
import KaiECOMarketplaceV2 from 'abis/KaiECOMarketplaceV2.json';
import { useWallet } from '@binance-chain/bsc-use-wallet';

const chainId = parseInt(process.env.REACT_APP_CHAIN_ID, 10);
export const ChainID = () => {
    const wallet = useWallet()
    return wallet.chainId;
}

export const getKaiECO721Address = () =>{
    return KaiECO721.networks[chainId].address;
}

export const getKaiECOMarketplaceAddress = () =>{
    return "0x9635cd68eA55B3294C436F903a3cC7aD0059F8C1";
}