import { useEffect, useState } from 'react'
import BigNumber from 'bignumber.js'
import { useWeb3React } from '@web3-react/core'
import { BIG_ZERO } from 'utils/bigNumber'
import useRefresh from './useRefresh'
import { getContract, getWeb3 } from 'utils/web3'
import Bep20ABI from '../constants/abi/Bep20.json';
import { useWallet } from '@binance-chain/bsc-use-wallet'

export const useTokenBalance = (tokenAddress: string) => {
    const [balance, setBalance] = useState(BIG_ZERO)
    const { account } = useWallet()
    const { fastRefresh } = useRefresh()
  
    useEffect(() => {
      const fetchBalance = async () => {
        const contract = getContract(Bep20ABI,tokenAddress);
        const res = await contract.methods.balanceOf(account).call()
        setBalance(new BigNumber(res))
      }
  
      if (account) {
        fetchBalance()
      }
    }, [account, tokenAddress,  fastRefresh])
  
    return balance
  }

  export const useBNBBalance = () => {
    const [balance, setBalance] = useState(BIG_ZERO)
    const { account } = useWallet()
    const { fastRefresh } = useRefresh()
    const web3 = getWeb3()
    useEffect(() => {
      const fetchBalance = async () => {
        const res = await web3.eth.getBalance(account);
        setBalance(new BigNumber(res))
      }
  
      if (account) {
        fetchBalance()
      }
    }, [account,   fastRefresh])
  
    return balance
  
  }