function AddWallet(wallet) {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ wallet })
    };
    return fetch(`${apiConstants.apiUrl}/api/wallet/create`, requestOptions)
        .then(handleResponse)
        .then(token => { 
            return token;
        }).catch(e => {
            console.log(e);
        });
}

