
import { constants } from "buffer";
import { apiConstants } from '../constants';

import { actionConstants } from "constants/actionConstants";
import { useKaiECOERC721Contract } from "hooks/useContract";
import {marketplaceService} from 'services/marketplaceService'



export const loadItem = async(dispatch, marketplaceContract , itemCount) => {
    console.log(marketplaceContract);
    console.log(itemCount)
    let items = [];
    for (let i = 0; i < itemCount; i++) {
        const item = await marketplaceContract.methods.getMarketItem(i + 1).call();
        items.push(item);
    }
    
    let assets= await Promise.all(items.map(async(item) => {
            let tokenData = await marketplaceService.getTokenIPFS(`${item.tokenUri}`);
            let asset = {
                auctionTime : item.auctionTime,
                createTime : item.createTime,
                creator : item.creator,
                itemId : item.itemId,
                contract : item.nftContract,
                owner : item.owner,
                price : item.price,
                isRemoved : item.isRemoved,
                tokenId : item.tokenId,
                token : item.tokenContract,
                image: `https://ipfs.io/ipfs/${tokenData.image}`,
                name: tokenData.name,
                description: tokenData.description,
    
            }
            return asset;
        }));
    dispatch({ type: actionConstants.LOAD_ITEM, payload: assets });   
}




export const loadCollection = async(marketplaceContract,dispatch) => {
    let collections = await marketplaceContract.methods.viewCollections().call();
    let position = 0;
    let assets = await Promise.all(collections.collectionDetails.map(async(item) => {
        let collectionAddress = collections.collectionAddresses[position];
        let asset = {
            symbol : item.symbol,
            name : item.name,
            description : item.description,
            uri : `https://ipfs.io/ipfs/${item.uri}`,
            collectionId : item.collectionId,
            address : collectionAddress,
            creator : item.creatorAddress
        }
        position++;
        return asset;
    }));
    dispatch({ type: actionConstants.LOAD_COLLECTION, payload: assets }); 

}





export const loadSeller = async(dispatch) => {
    marketplaceService.getSeller()
        .then(
            res => {
                dispatch(success(res));

                
            }
        )
    function success(sellers) { return { type: actionConstants.LOAD_SELLER, payload: sellers } }
}

export const loadingCollection = async(dispatch, marketplaceContract, collectionCount) => {
    let items = [];
    for (let i = 0; i < collectionCount; i++) {
        const item = await marketplaceContract.methods.collectionItems(i + 1).call();
        items.push(item);
    }
    console.log(items)
    dispatch({ type: actionConstants.LOAD_ITEM, payload: items });  
}



export const loadUser = (account) => {
    return dispatch => {
        marketplaceService.getUser(account)
        .then(
            res => {
                if(res.length > 0){
                    dispatch(success(res));
                }
               
            }
        )
    }
    
    function success(user) { return { type: actionConstants.LOAD_USER, payload: user } }
}

export const updateUser = (address, name, customUrl, twitter, email, description, image) => {
    return dispatch => {
        marketplaceService.updateUser(address, name, customUrl, twitter, email, description, image)
        .then(
            res => {
                console.log(res);
                if(res.length > 0){
                    dispatch(update_user(res));
                }
            }
        )
    }
    function update_user(user) { return { type: actionConstants.LOAD_USER, payload: user } }
}

