import { actionConstants } from "constants/actionConstants";

const initState = {
    collectionCount : null,
    itemCount : null,
    items : [],
    collections : [],
    sellers : [],
    user : null,
}

const marketplaceReducer = (state = initState, action : any) =>{
    switch(action.type){
        case actionConstants.LOAD_ITEM_COUNT:
            return {
                ...state,
                itemCount: action.payload
            }

        case actionConstants.LOAD_ITEM:
            return {
                ...state,
                items: action.payload
            }
        case actionConstants.ADD_ITEM:
            let newItems = [ ...state.items, action.payload ];
            return {
                ...state,
                items : newItems
            }
        case actionConstants.UPDATE_ITEM:
            let changedItems = state.items?.map(t => {
                if(t.id === action.payload.id) return action.payload;
                return t;
            })
            return {
                ...state,
                items : changedItems
            }
        case actionConstants.LOAD_COLLECTION_COUNT:
            return {
                ...state,
                collectionCount: action.payload
            }

        case actionConstants.LOAD_COLLECTION:
            return {
                ...state,
                collections: action.payload
            }
        case actionConstants.ADD_COLLECTION:
            let newcollections = [ ...state.collections, action.payload ];
            return {
                ...state,
                collections : newcollections
            }
        case actionConstants.LOAD_SELLER:
            return {
                ...state,
                sellers: action.payload
            }
        case actionConstants.LOAD_USER:
            return {
                ...state,
                user: action.payload
            }
        default:
            return state    
    }
}

export default marketplaceReducer;