import useWeb3 from 'hooks/useWeb3'
import { useMemo } from 'react'
import {getKaiECOERC721Contract, getKaiECOMarketplaceContract, getNFTCollectionContract, getNFTMarketplaceContract, getKaiCollectionContract, getKaiECOContract, getBep20Contract} from 'utils/contractHelpers'

export const useBep20Contract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getBep20Contract(address, web3), [address, web3])
}

export const useNFTCollectionContract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getNFTCollectionContract(address, web3), [address, web3])
}

export const useNFTMarketplaceContract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getNFTMarketplaceContract(address, web3), [address, web3])
}

export const useKaiECOERC721Contract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getKaiECOERC721Contract(address, web3), [address, web3])
}


export const useKaiECOMarketplaceContract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getKaiECOMarketplaceContract(address, web3), [address, web3])
}

export const useKaiCollectionContract = (abi : any) => {
    const web3 = useWeb3();
    return useMemo(() => getKaiCollectionContract(abi, web3),[web3]);
}

export const useKaiECOContract = (address : string) => {
    const web3 = useWeb3();
    return useMemo(()=>getKaiECOContract(address, web3), [address, web3])
}

