import React, { useEffect, lazy, Suspense } from 'react';
import { BrowserRouter as Router, Route, Switch, useHistory } from 'react-router-dom'
import './App.css';
//import { useWallet } from '@binance-chain/bsc-use-wallet';


import Header from "./components/Header/Header";
import WalletConnect from './views/WalletConnect';


import Footer from 'components/Footer/Footer';
import ModalUser from 'components/Modal/ModalUser';
import ModalSearch from 'components/Modal/ModalSearch';
import ModalMenu from 'components/Modal/ModalMenu';
import ModalNavMenu from 'components/Modal/ModalNavMenu';
import Scrollup from 'components/Scrollup/Scrollup';

import { useWallet } from '@binance-chain/bsc-use-wallet';
import { useDispatch } from 'react-redux';
import { getKaiECO721Address, getKaiECOMarketplaceAddress } from 'utils/addressHelpers';
import { useKaiECOMarketplaceContract } from 'hooks/useContract';
import { loadCollection, loadItem, loadSeller, loadUser } from 'actions';
import { marketplaceService } from 'services';


const Home = lazy(() => import('./views/Home'));
const Create = lazy(() => import('./views/Create'));
const User = lazy(() => import('./views/User'));
const Setting = lazy(() => import('./views/Setting'));
const Collection = lazy(() => import('./views/Collection'));
const Token = lazy(() => import('./views/Token'));
const Sellers = lazy(() => import('./views/Sellers'));
const Auctions = lazy(() => import('./views/Auction'));
const Collections = lazy(() => import('./views/TopCollections'));
const Explores = lazy(() => import('./views/Explores'));
const MyCollections = lazy(() => import('./views/MyCollections'));




const App: React.FC = () => {

  const { account, connect, chainId } = useWallet()
  const dispatch = useDispatch();
  const marketplaceAddress = getKaiECOMarketplaceAddress();
  const marketplaceContract = useKaiECOMarketplaceContract(marketplaceAddress);

  useEffect(() => {
    const loadData = async () => {
      await loadCollection(marketplaceContract, dispatch);
      
      //dispatch({ type: actionConstants.LOAD_ITEM_COUNT, payload: itemCount })
      //await loadItem(dispatch, marketplaceContract, itemCount);
      await loadSeller(dispatch);
      
    }
    loadData()
  }, [])

  useEffect(() => {

    const loadData = async () => {
      await loadCollection(marketplaceContract, dispatch);
     
      //const itemCount = await marketplaceContract.methods.itemCount().call()
      //dispatch({ type: actionConstants.LOAD_ITEM_COUNT, payload: itemCount })
      //await loadItem(dispatch, marketplaceContract, itemCount);
      await loadSeller(dispatch);
    }

    const interval = setInterval(() => {
      loadData()
    }, 20000);
    return () => clearInterval(interval);
  }, [])


  useEffect(() => {
    if (!account && window.localStorage.getItem('accountStatus')) {
      connect('injected')
    }
    if (account != null) {
      loadUser(account);
      marketplaceService.AddWallet(account);
    }

  }, [account, connect])



  return (
    <Router>

      <div className='main'>

        
        <Suspense fallback={<div>Loading</div>}>
          <Header />
          <Switch>
            <Route path="/" component={Home} exact />
            <Route path="/create" component={Create} />
            <Route path="/connect" component={WalletConnect} />
            <Route path="/auctions" component={Auctions} />
            <Route path="/collections" component={Collections} />
            <Route path="/sellers" component={Sellers} />
            <Route path="/explore" component={Explores} />
            <Route path="/user/:wallet" component={User} />
            <Route path="/setting/:account" component={Setting} />
            <Route path="/collection/:collection" component={Collection} />
            <Route path="/mycollections/:acoount" component={MyCollections} />
            <Route path="/token/:collection/:tokenId" component={Token} />
            <Route path="/seller/:seller" component={Sellers} />
          </Switch>
          <Footer />
        </Suspense>
        
        <ModalUser />
        <ModalSearch />
        <ModalNavMenu />
        <ModalMenu />
        <Scrollup />

      </div>
    </Router>
  );
}

export default React.memo(App);
